import React from "react";
import { Switch, Route } from "react-router-dom";
import { BackIconButton, Content, Header, Page } from "mastro-elfo-mui";
import DrawerIcon from "@material-ui/icons/Description";

import DocContent from "./doc/content";

function Component() {
  return (
    <Switch>
      <Route path="/doc" exact>
        <Page
          header={
            <Header LeftAction={<BackIconButton />}>Documentation</Header>
          }
          content={<Content>Coming soon</Content>}
        />
      </Route>
      <Route path="/doc/:key" component={DocContent} />
    </Switch>
  );
}

export const route = {
  path: "/doc",
  component: Component,
};

export const drawer = {
  key: "doc",
  primary: "Doc",
  secondary: "",
  icon: <DrawerIcon />,
  title: "Open Doc",
};
