import React from "react";
import { useHistory } from "react-router-dom";
import {
  Content,
  DrawerIconButton,
  DrawerLists,
  Header,
  Page,
} from "mastro-elfo-mui";

import { drawer as doc } from "./doc";
import { drawer as license } from "./license";

function Component() {
  const { push } = useHistory();

  return (
    <Page
      header={
        <Header
          LeftAction={
            <DrawerIconButton>
              <DrawerLists
                lists={[
                  {
                    key: "doc",
                    items: [{ ...doc, onClick: () => push("/doc") }],
                  },
                  {
                    key: "source",
                    header: "Source",
                    items: [license],
                  },
                ]}
              />
            </DrawerIconButton>
          }
        >
          me-mui-doc
        </Header>
      }
      content={<Content>Home content</Content>}
    />
  );
}

export const route = {
  path: "/",
  exact: true,
  component: Component,
};
